n    box-shadow: $button-default-box-shadow rgba($bg-accent_secondary, 0.5) inset;\n    font-size: $font-size-xs;\n    font-weight: $font-weight-dark;\n    color: $txt-white;\n\n    @include respond-to('small') {\n      font-size: $font-size-xxs;\n      font-weight: $font-weight-normal;\n      color: $txt-white;\n      padding: $button-primary-padding-small;\n      border-radius: $button-default-border-radius-small;\n    }\n\n    &:hover {\n      background: $bg-button-primary-hover;\n      border-color: $border-button-primary-hover;\n      box-shadow: $button-default-box-shadow rgba($border-button-primary-hover, 0.5) inset;\n    }\n\n    &:active {\n      background: $bg-button-primary-clicked;\n      border-color: $border-button-primary-clicked;\n      box-shadow: $button-default-box-shadow rgba($border-button-primary-clicked, 0.5) inset;\n    }\n  }\n\n  &.secondary {\n    @extend .button-base;\n    background: $border-accent;\n    padding: $button-secondary-padding;\n    border-color: $bg-accent_secondary;\n    box-shadow:\n      $button-secondary-box-shadow $bg-accent_secondary inset,\n      $button-secondary-inner-shadow $bg-button-secondary inset;\n\n    &:hover {\n      background: $bg-button-primary-hover;\n      border-color: $border-button-primary-hover;\n      box-shadow:\n        $button-secondary-box-shadow $border-button-primary-hover inset,\n        $button-secondary-inner-shadow $bg-button-secondary inset;\n    }\n\n    &:active {\n      background: $bg-button-primary-clicked;\n      border-color: $border-button-primary-clicked;\n      box-shadow:\n        $button-secondary-box-shadow $border-button-primary-clicked inset,\n        $button-secondary-inner-shadow $bg-button-secondary inset;\n    }\n  }\n  &.secondary--green {\n    @extend .button-base;\n    background: $bg-green_base;\n    padding: $button-secondary-padding;\n    border-color: $border-green_dark;\n    box-shadow:\n      $button-secondary-box-shadow $border-green_dark inset,\n      $button-secondary-inner-shadow $bg-green-gradient inset;\n\n    @include respond-to('small') {\n      font-size: $font-size-xxs;\n      font-weight: $font-weight-normal;\n      color: $txt-white;\n      padding: $button-secondary-padding-small;\n      border-radius: $button-default-border-radius-small;\n      min-width: 40px;\n      min-height: 40px;\n    }\n\n    &:hover {\n      background: $bg-green-base-hover;\n      border-color: $border-button-green-hover;\n      box-shadow:\n        $button-secondary-box-shadow $border-button-green-hover inset,\n        $button-secondary-inner-shadow $bg-green-gradient inset;\n    }\n\n    &:active {\n      background: $bg-green-base-clicked;\n      border-color: $border-button-green-clicked;\n      box-shadow:\n        $button-secondary-box-shadow $border-button-green-clicked inset,\n        $button-secondary-inner-shadow $bg-green-gradient inset;\n    }\n  }\n\n  &.replay {\n    // This is also used by the Hearts and Flowers task\n    position: absolute;\n    top: 20px;\n    right: 20px;\n    cursor: pointer;\n    border-radius: 16px;\n    padding: 16px;\n    background-color: rgba($bg-button-primary, 0.2);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 64px;\n    height: 64px;\n    border: 2px solid $border-button-accent;\n\n    @include respond-to('small') {\n      width: 48px;\n      height: 48px;\n      padding: 12px;\n    }\n\n    &:hover {\n      background-color: rgba($bg-button-primary-hover, 0.4);\n      border: 2px solid $border-button-primary-hover;\n    }\n\n    &:active {\n      background-color: rgba($bg-button-primary-clicked, 0.6);\n      border: 2px solid $border-button-primary-clicked;\n    }\n  }\n}\n","$button-primary-padding: 20px 72px;\n$button-primary-padding-small: 12px 60px;\n$button-secondary-padding: 48px 48px;\n$button-secondary-padding-small: 40px 40px;\n$button-default-border-radius: 32px;\n$button-default-border-radius-small: 24px;\n$button-default-border-width: 6px;\n$button-default-box-shadow: -6px -8px 0px 0px;\n$button-default-font-size: 3vw;\n$button-default-font-weight: 500;\n$button-secondary-box-shadow: -8px -10px 0px 0px;\n$button-secondary-inner-shadow: 0px 0px 12px 0px;\n","$bg-button-primary: #275bdd;\n$bg-button-primary-hover: #2051cb;\n$bg-button-primary-clicked: #1d4ab9;\n$bg-accent_secondary: #0f3cac;\n$bg-button-secondary: #d4def8;\n$bg-button-shadow: #e7ecf7;\n$bg-green_dark: #5ba300;\n$bg-green_base: #89ce00;\n$bg-green-base-hover: #7ab800;\n$bg-green-base-clicked: #70a800;\n$bg-green-gradient: #cfebb6;\n$bg-black_base: #010d18;\n$bg-grey-light: #ededed;\n$bg-bubble-base: #ffffff;\n$bg-white-30: #ffffff4d;\n$bg-white-40: #ffffff66;\n$bg-white-70: #ffffffb3;\n$bg-white-80: #ffffffcc;\n$bg-orange_base: #ffa063;\n$bg-yellow_base: #ffe8ac;\n$bg-red_base: #ff5757;\n$bg-purple_base: #b285c5;\n$bg-workspace-base: #d7e3fd;\n\n$txt-primary: #275bdd;\n$txt-secondary: #0f3cac;\n$txt-white: #fbfbfd;\n\n$border-grey: #bdbdbd;\n$border-button-accent: #0f3cac;\n$border-button-primary-hover: #0d3496;\n$border-button-primary-clicked: #0b2e83;\n$border-button-green-hover: #4d8a00;\n$border-button-green-clicked: #539400;\n$border-button-secondary: #a0bbff;\n$border-green_dark: #5ba300;\n$border-accent: #275bdd;\n$border-purple_dark: #813a9f;\n$border-orange_dark: #e3630a;\n$border-yellow_dark: #ffc839;\n$border-red_dark: #930b0b;\n","$font-size-xxl: 85px;\n$font-size-xl: 72px;\n$font-size-l: 64px;\n$font-size-m: 48px;\n$font-size-s: 40px;\n$font-size-xs: 32px;\n$font-size-xxs: 24px;\n$font-size-xxxs: 16px;\n\n$font-weight-dark: 700;\n$font-weight-bold: 600;\n$font-weight-normal: 500;\n$font-weight-light: 400;\n$font-weight-extra-light: 300;\n","$breakpoints: (\n  'small': (\n    max-width: 500px,\n  ),\n  'medium': (\n    max-width: 800px,\n  ),\n  'large': (\n    max-width: 1000px,\n  ),\n  'very-large': (\n    min-width: 1000px,\n  ),\n  'huge': (\n    min-width: 1200px,\n  ),\n);\n\n/// Responsive breakpoint manager\n/// @access public\n/// @param {String} $breakpoint - Breakpoint\n/// @requires $breakpoints\n@mixin respond-to($breakpoint) {\n  $raw-query: map-get($breakpoints, $breakpoint);\n\n  @if $raw-query {\n    $query: if(type-of($raw-query) == 'string', unquote($raw-query), inspect($raw-query));\n\n    @media #{$query} {\n      @content;\n    }\n  } @else {\n    @error 'No value found for `#{$breakpoint}`. '\n         + 'Please make sure it is defined in `$breakpoints` map.';\n  }\n}\n","@use '../abstracts' as *;\n\n.lev-text {\n  font-size: 32px;\n  line-height: 1;\n  color: $txt-primary;\n  letter-spacing: 0;\n  font-weight: 700;\n\n  &.primary {\n    color: $txt-primary;\n  }\n\n  &.secondary {\n    color: $txt-secondary;\n  }\n\n  &.base {\n    color: $txt-white;\n  }\n\n  &.h0 {\n    font-size: 85px;\n  }\n\n  &.h1 {\n    font-size: 72px;\n  }\n\n  &.h2 {\n    font-size: 64px;\n  }\n\n  &.h3 {\n    font-size: 48px;\n    line-height: 1.5;\n  }\n\n  &.h4 {\n    font-size: 40px;\n    line-height: 1.5;\n  }\n\n  &.main-bold {\n    font-size: 32px;\n    line-height: 1.5;\n  }\n\n  &.main-regular {\n    font-size: 32px;\n    line-height: 1.5;\n    font-size: 500;\n  }\n}\n","@use '../abstracts/' as *;\n\n.lev-response-row {\n  width: 100%;\n  &.linear-4 {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n\n    .response-container {\n      display: flex;\n      width: calc(25% - 30px);\n      min-width: 150px;\n      max-width: 200px;\n      aspect-ratio: 1;\n      font-size: $font-size-xxl;\n      font-weight: $font-weight-dark;\n      color: $txt-white;\n      justify-content: center;\n      align-items: center;\n\n      @include respond-to('medium') {\n        display: flex;\n        width: calc(25% - 10px);\n        aspect-ratio: 1;\n        font-size: $font-size-l;\n        font-weight: $font-weight-bold;\n        color: $txt-primary;\n        justify-content: center;\n        align-items: center;\n      }\n      @include respond-to('small') {\n        display: flex;\n        width: calc(25% - 10px);\n        aspect-ratio: 1;\n        font-size: $font-size-m;\n        font-weight: $font-weight-normal;\n        color: $txt-secondary;\n        justify-content: center;\n        align-items: center;\n      }\n\n      &--small {\n        @extend .response-container;\n        max-width: 150px;\n      }\n    }\n  }\n}\n","@use '../abstracts/' as *;\n\n.lev-row-container {\n  display: flex;\n  padding: $lev-spacing-s;\n  justify-content: center;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  color: $txt-primary;\n\n  &.header {\n    p {\n      font-size: $font-size-m;\n      font-weight: $font-weight-bold;\n      margin: 0;\n    }\n\n    @include respond-to('medium') {\n      display: flex;\n      padding: $lev-spacing-xs;\n      justify-content: center;\n      align-items: center;\n\n      p {\n        font-size: $font-size-s;\n        font-weight: $font-weight-normal;\n        margin: 0;\n      }\n    }\n\n    @include respond-to('small') {\n      display: flex;\n      padding: $lev-spacing-xxs;\n      justify-content: center;\n      align-items: center;\n\n      p {\n        font-size: $font-size-xxs;\n        font-weight: $font-weight-normal;\n        margin: 0;\n      }\n    }\n  }\n\n  &.instruction {\n    display: flex;\n    padding: $lev-spacing-s;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    border: 3px solid $bg-accent_secondary;\n    border-radius: 32px;\n    background-color: $bg-bubble-base;\n\n    p {\n      font-size: $font-size-xs;\n      font-weight: $font-weight-light;\n      line-height: 1;\n      margin: 0;\n    }\n\n    @include respond-to('medium') {\n      display: flex;\n      padding: $lev-spacing-xs;\n      justify-content: center;\n      align-items: center;\n      border-radius: 24px;\n\n      p {\n        font-size: $font-size-xxs;\n        font-weight: $font-weight-light;\n        line-height: 1;\n        margin: 0;\n      }\n    }\n\n    @include respond-to('small') {\n      display: flex;\n      padding: $lev-spacing-xxs;\n      justify-content: center;\n      align-items: center;\n      border-radius: 16px;\n\n      p {\n        font-size: $font-size-xxxs;\n        font-weight: $font-weight-light;\n        line-height: 1;\n        margin: 0;\n      }\n    }\n  }\n}\n","$lev-spacing-xxl: 64px;\n$lev-spacing-xl: 48px;\n$lev-spacing-l: 40px;\n$lev-spacing-m: 32px;\n$lev-spacing-s: 24px;\n$lev-spacing-xs: 16px;\n$lev-spacing-xxs: 12px;\n$lev-spacing-xxxs: 8px;\n","@use '../abstracts/' as *;\n\n.haf-parent-container {\n  width: 100%;\n  max-width: 900px;\n}\n\n.haf-stimulus-holder {\n  margin-top: $lev-spacing-l;\n}\n\n.haf-stimulus-container {\n  border: 3px solid $border-accent;\n  border-radius: 32px;\n  display: flex;\n  justify-content: space-between;\n  background-color: #fff;\n  height: 40vh;\n  margin-top: 16px;\n\n  .stimulus-left {\n    margin-top: auto;\n    margin-bottom: auto;\n    padding-left: 2rem;\n    padding-right: 2rem;\n    @include respond-to('small') {\n      padding-left: 12px;\n      padding-right: 12px;\n      img {\n        width: 70px;\n        height: 70px;\n      }\n    }\n  }\n\n  .stimulus-right {\n    margin-top: auto;\n    margin-bottom: auto;\n    margin-left: auto;\n    padding-left: 2rem;\n    padding-right: 2rem;\n    @include respond-to('small') {\n      padding-left: 12px;\n      padding-right: 12px;\n      img {\n        width: 70px;\n        height: 70px;\n      }\n    }\n  }\n}\n\n.haf-image-container {\n  display: flex;\n  justify-content: center;\n  margin-bottom: 16px;\n  margin-top: 8px;\n\n  img {\n    width: 220px;\n    max-width: 40%;\n  }\n\n  @include respond-to('medium') {\n    img {\n      width: 180px;\n    }\n  }\n\n  @include respond-to('small') {\n    img {\n      width: 120px;\n    }\n  }\n}\n\n.haf-fixation-container {\n  border: 3px solid $border-accent;\n  border-radius: 32px;\n  background-color: #fff;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 40vh;\n  margin-top: $lev-spacing-xs + $lev-spacing-s;\n\n  .fixation {\n    font-size: 7vh;\n  }\n}\n\n.haf-cr-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  p {\n    margin-top: 16px;\n  }\n}\n","// Will need to namespace classes and id's\n@use 'index';\n// Breakpoints\n$breakpoint-tablet: 600px;\n$breakpoint-desktop: 1024px;\n$breakpoint-large-desktop: 1440px;\n\n// Colors\n$border-color: #0f3cac;\n\n.toast {\n  // Initial style for the toast\n  position: fixed;\n  top: -100px; // Start off-screen\n  left: 50%;\n  transform: translateX(-50%);\n  background-color: white;\n  color: #0f3cac;\n  padding: 10px 20px;\n  border: #275bdd solid 3px;\n  border-radius: 1rem;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\n  z-index: 1000;\n  display: block;\n  transition: top 0.5s ease;\n  font-size: 1.5rem;\n\n  @media (min-width: $breakpoint-tablet) {\n    font-size: 1.2rem;\n  }\n\n  &.show {\n    top: 10vh; // Final position\n\n    @media (min-width: $breakpoint-tablet) {\n      top: 5vh;\n    }\n  }\n}\n\n.jspsych-display-element {\n  height: 100%;\n  width: 100%;\n  margin: 0;\n  max-width: 100%;\n  overflow: hidden; // disable scrolling\n  font-family: 'Source Sans Pro', Gotham, 'Helvetica Neue', Helvetica, Arial, 'sans-serif';\n  touch-action: manipulation;\n  -webkit-touch-callout: none; /* iOS Safari */\n  -webkit-user-select: none; /* Safari */\n  -khtml-user-select: none; /* Konqueror HTML */\n  -moz-user-select: none; /* Old versions of Firefox */\n  -ms-user-select: none; /* Internet Explorer/Edge */\n  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */\n  color: #0f3cac;\n  background-image: url('data:image/png;base64,');\n  background-size: cover;\n\n  .jspsych-content-wrapper {\n    height: 100%;\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    .jspsych-content {\n      height: 100%;\n      width: 100%;\n      display: flex;\n      flex-direction: column;\n      justify-content: center;\n      align-items: center;\n      padding: 2vw;\n\n      #fullscreen {\n        width: 80vw;\n        text-align: center;\n        // color: rgba(76, 101, 139, 1);\n        font-size: 1.7rem;\n      }\n\n      #instruction-video {\n        height: 60vh;\n        width: 40vw;\n\n        @media (min-width: $breakpoint-tablet) {\n          height: 50vh;\n          width: 30vw;\n        }\n\n        @media (min-width: $breakpoint-desktop) {\n          height: 55vh;\n          width: 35vw;\n        }\n      }\n\n      #continue-btn {\n        background-color: #275bdd;\n        color: white;\n        border-radius: 2rem;\n        border: 6px solid #0f3cac;\n        padding: 1rem;\n        font-size: 1.5rem;\n        cursor: pointer;\n        margin-top: 1.5rem;\n\n        @media (min-width: $breakpoint-tablet) {\n          margin-top: 0.8rem;\n        }\n\n        @media (min-width: $breakpoint-desktop) {\n          margin-top: 1.5rem;\n        }\n      }\n\n      #jspsych-fullscreen-btn {\n        width: 17vw;\n        height: 10vh;\n        background-color: #275bdd;\n        color: white;\n        border-radius: 2rem;\n        border: 6px solid #0f3cac;\n        font-size: 3vw;\n        font-weight: bold;\n        cursor: pointer;\n      }\n\n      .instructions-container {\n        text-align: center;\n        font-size: 1.3rem;\n\n        .instructions-title {\n          text-align: center;\n          margin-bottom: 3vh;\n          font-size: 2rem;\n        }\n\n        p {\n          text-align: left;\n          margin: 0 auto;\n          // max-width: 60vw;\n          line-height: 1.5;\n        }\n\n        footer {\n          margin-top: 1.5rem;\n        }\n      }\n\n      #footer {\n        font-size: 1.2rem;\n      }\n\n      // Multichoice template\n      #fixation-container {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        color: black;\n        font-size: 4rem;\n      }\n\n      #stimulus-container {\n        margin: auto;\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        margin-top: 2rem;\n        width: 100%;\n\n        #instruction-graphic {\n          width: 40%;\n        }\n\n        .centered-container-haf {\n          display: flex;\n          justify-content: center;\n        }\n\n        #prompt-container-text {\n          background-color: #fff;\n          border: 3px solid #275bdd;\n          border-radius: 2rem;\n          margin: 1rem 0;\n          color: #275bdd;\n          padding: 0 0.5rem;\n          width: 95%;\n\n          #prompt {\n            font-size: 2rem;\n            text-align: center;\n\n            @media (min-width: $breakpoint-tablet) {\n              font-size: 1.2rem;\n            }\n\n            @media (min-width: $breakpoint-desktop) {\n              font-size: 1.5rem;\n            }\n          }\n        }\n\n        #stimulus-html {\n          padding-bottom: 3rem;\n          font-size: 3rem;\n          margin: 0;\n          text-align: center; /* To ensure the text is centered if it wraps to more than one line */\n        }\n\n        #stimulus-img {\n          margin-bottom: 1rem;\n          width: 28vw;\n          border: 3px solid #0f3cac;\n          border-radius: 1rem;\n        }\n      }\n\n      .practice-correct {\n        box-shadow: 0 0 20px 5px rgb(85, 251, 56);\n        border-radius: 2rem;\n      }\n\n      .practice-incorrect {\n        box-shadow: 0 0 30px 8px rgb(255, 43, 1);\n        border-radius: 2rem;\n      }\n\n      // 1 x 4 (default)\n      .default-layout {\n        display: flex;\n        // flex-wrap: wrap;\n        // justify-content: space-around;\n        max-width: 100%;\n        gap: 1.5rem;\n\n        div {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          max-width: calc(25% + 10px);\n        }\n\n        .two-afc {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          max-width: calc(50% + 10px);\n        }\n      }\n\n      // 4 x 1\n      .column-layout {\n        display: grid;\n        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* two columns of equal width */\n        gap: 2rem;\n        place-items: center;\n\n        div {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          // 100%\n          max-width: 50%;\n\n          button {\n            width: 100%;\n            cursor: pointer;\n            background-color: rgb(0, 132, 255);\n            border: none;\n            border-radius: 0.5rem;\n            color: white;\n            padding: 1.5rem;\n            font-size: 1.5rem;\n          }\n        }\n      }\n\n      // 2 x 2\n      .grid-layout {\n        display: grid;\n        grid-template-columns: 1fr 1fr; /* two columns of equal width */\n        grid-template-rows: 1fr 1fr; /* two rows of equal height */\n        gap: 2rem;\n        width: 20rem;\n        margin-left: auto;\n        margin-right: auto;\n\n        div {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          max-width: 100%;\n        }\n      }\n\n      // pyramid\n      .triple-layout {\n        display: grid;\n        grid-template-columns: repeat(3, 1fr); /* three columns of equal width */\n        grid-template-rows: auto; /* rows sized according to content */\n        gap: 2rem;\n\n        div {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          max-width: 100%;\n\n          button {\n            width: 100%;\n            cursor: pointer;\n            background-color: rgb(0, 132, 255);\n            border: none;\n            border-radius: 0.5rem;\n            color: white;\n            padding: 1.5rem;\n            font-size: 1.5rem;\n          }\n        }\n\n        /* Specific styles for individual buttons */\n        .button1 {\n          grid-column: 2; /* Centered in the middle column */\n          grid-row: 1;\n        }\n\n        .button2 {\n          grid-column: 1; /* First column in the second row */\n          grid-row: 2;\n        }\n\n        .button3 {\n          grid-column: 3; /* Third column in the second row */\n          grid-row: 2;\n        }\n      }\n\n      .diamond-layout {\n        display: grid;\n        grid-template-columns: repeat(3, 1fr); /* three columns of equal width */\n        grid-template-rows: auto auto; /* three rows, size according to content */\n        gap: 2rem;\n        // width: 20rem;\n        margin-left: auto;\n        margin-right: auto;\n\n        div {\n          box-sizing: border-box;\n          margin: 0;\n          padding: 0;\n          max-width: 100%;\n          align-self: start; /* Aligns button content to the start of the grid area */\n        }\n\n        .button1 {\n          grid-column: 2; /* Centered in the middle column */\n          grid-row: 1;\n        }\n\n        .button2 {\n          grid-column: 1; /* First column */\n          grid-row: 1 / 3; /* Spanning from the first row to the third grid line */\n          align-self: center; /* Center this button vertically */\n        }\n\n        .button3 {\n          grid-column: 3; /* Third column */\n          grid-row: 1 / 3; /* Spanning from the first row to the third grid line */\n          align-self: center; /* Center this button vertically */\n        }\n\n        .button4 {\n          grid-column: 2; /* Centered in the middle column */\n          grid-row: 2;\n        }\n      }\n\n      .math-btn {\n        display: flex;\n        width: 12rem;\n        height: 12rem;\n        padding: 8px;\n        justify-content: center;\n        align-items: center;\n        gap: 8px;\n        border-radius: 32px;\n        border: 6px solid #0f3cac;\n        background: #275bdd;\n        // text\n        color: #fbfbfd;\n        font-family: 'Noto Sans';\n        font-size: 10vh;\n        font-style: normal;\n        font-weight: 700;\n        line-height: normal;\n        cursor: pointer;\n      }\n\n      .img-btn {\n        background-color: #fff;\n        border: #0f3cac solid 6px;\n        border-radius: 2rem;\n        cursor: pointer;\n\n        img {\n          border: none;\n          width: 12vw; // 12rem\n          height: 12vw; // 12rem\n          padding: 0;\n          margin: 0;\n          border-radius: 2rem;\n        }\n      }\n\n      // #selected-btn {\n      //   background-color: #2200ff; /* Change the background color to indicate selection */\n      //   color: #fff; /* Change the text color for better visibility */\n      //   border: 5px solid #32f718; /* Add a border to highlight the button */\n      // }\n\n      .arrow-key-border {\n        border: 0px solid black; /* Thin and black border */\n        border-radius: 10px; /* Curved corners */\n        padding: 0.1rem !important;\n        max-width: 100% !important;\n        pointer-events: none;\n\n        .arrow-key {\n          font-size: 1.5rem;\n          padding: 0;\n          margin: 0;\n          pointer-events: none;\n        }\n      }\n\n      .space-key-border {\n        border: 1px solid black; /* Thin and black border */\n        border-radius: 10px; /* Curved corners */\n        max-width: 100% !important;\n        margin-top: 1rem;\n        padding: 1rem 3rem;\n        cursor: pointer;\n\n        .space-key {\n          font-size: 1.5rem;\n          margin: 0;\n        }\n      }\n\n      .disabled-btn {\n        background-color: #e0e0e0; /* Gray background */\n        color: #a0a0a0; /* Gray text */\n        cursor: not-allowed; /* Indicate that the button is not interactive */\n        pointer-events: none; /* Prevent all click and hover interactions */\n        opacity: 0.6;\n      }\n\n      .glowingButton {\n        animation: glowing 1300ms infinite;\n      }\n\n      @keyframes glowing {\n        0% {\n          background-color: #2ba805;\n          box-shadow: 0 0 5px #2ba805;\n        }\n        50% {\n          background-color: #49e819;\n          box-shadow: 0 0 20px #49e819;\n        }\n        100% {\n          background-color: #2ba805;\n          box-shadow: 0 0 5px #2ba805;\n        }\n      }\n\n      .prompt-container {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        flex-direction: column;\n\n        .task-graphic {\n          width: 20%;\n        }\n      }\n\n      // ----------------------------------------------------------------------------\n\n      input[type='range'] {\n        --line-height: 2px;\n        --line-color: gray;\n        --tick-width: 5px;\n        --tick-height-minmax: 50px;\n        --tick-height-target: 30px;\n        --tick-color-minmax: black;\n        --tick-color-target: red;\n\n        @mixin track {\n          height: 0;\n          border: var(--line-height) solid var(--line-color);\n        }\n\n        @mixin thumb {\n          background: var(--tick-color-target);\n          width: var(--tick-width);\n          height: var(--tick-height-target);\n        }\n\n        appearance: none;\n        background: transparent;\n        margin: 0;\n        width: 100%;\n        height: var(--tick-height-minmax);\n        border-left: var(--tick-width) solid var(--tick-color-minmax);\n        border-right: var(--tick-width) solid var(--tick-color-minmax);\n        box-sizing: border-box;\n\n        // WebKit-specific: Chrome, Safari, new Edge\n        &::-webkit-slider-runnable-track {\n          @include track;\n        }\n\n        // Firefox\n        &::-moz-range-track {\n          @include track;\n        }\n\n        // Chrome, Safari, and Edge Chromium\n        &::-webkit-slider-thumb {\n          @include thumb;\n          appearance: none;\n          transform: translateY(-50%);\n        }\n\n        // Firefox\n        &::-moz-range-thumb {\n          @include thumb;\n          border: none;\n          border-radius: 0;\n        }\n\n        &:enabled::-webkit-slider-thumb {\n          cursor: pointer;\n        }\n\n        &:enabled::-moz-range-thumb {\n          cursor: pointer;\n        }\n      }\n\n      // Math-Egma\n      #jspsych-html-slider-response-wrapper {\n        width: min(800px, 95%);\n        max-height: 95%;\n        margin: 0 !important;\n      }\n\n      .jspsych-html-slider-response-container {\n        width: 100% !important;\n      }\n\n      #jspsych-html-slider-response-response {\n        width: 100%;\n      }\n\n      #jspsych-html-slider-response-stimulus {\n        text-align: center;\n        margin-bottom: 5rem;\n      }\n\n      #slider-btn-container {\n        margin-top: 10vh;\n        display: flex;\n        justify-content: space-around;\n      }\n\n      #jspsych-html-slider-response-next {\n        padding: 1rem 3rem;\n        color: white;\n        background-color: #2ba805;\n        border: none;\n        border-radius: 0.5rem;\n        cursor: pointer;\n        font-size: 1.5rem;\n      }\n\n      #replay-btn {\n        // This is also used by the Hearts and Flowers task\n        position: absolute;\n        top: 20px;\n        right: 20px;\n        cursor: pointer;\n        border-radius: 1rem;\n        padding: 1rem;\n        background-color: rgba(39, 91, 221, 0.2);\n        display: flex;\n        justify-content: center;\n        align-items: center;\n\n        @media (min-width: $breakpoint-tablet) {\n          width: 2rem;\n          height: 2rem;\n          padding: 0.8rem;\n        }\n\n        @media (min-width: $breakpoint-desktop) {\n          width: 3rem;\n          height: 3rem;\n        }\n      }\n\n      // ----------------------------------------------------------------------------\n      // Matrix Reasoning\n      .response-feedback-container {\n        button {\n          position: relative;\n          img {